<template>
  <el-row>
    <el-col  style="margin-bottom: 15px">
      <h2 class="text-center hand-written"> Welcome to my note book <el-icon><notebook /></el-icon> <el-icon><milk-tea /></el-icon></h2>
    </el-col>
  </el-row>
  
  <el-row v-loading="loading" :gutter="20" style="margin-bottom: 10px">
    <el-col :span="18" :offset="3" v-for="post in posts" :key="post.id">
      <el-card :body-style="{ padding: '0px' }" shadow="hover" style="martin-top: 30px; margin-bottom: 30px">
        <el-row style="padding: 14px" align="middle" justify="center" v-if="post.image && post.image != ''">
          <el-col :xs="24" :sm="24" :md="14" :lg="16" :xl="12">
            <p class="text-center">
              <b>{{ post.title }}</b>
            </p>
            <div class="bottom">
              {{ post.summary }} ..
            </div>
            <p class="text-center">
              <el-link :underline="false" type="primary" :href="'/blog/posts/' + post.slug">      
                Read more
              </el-link>
            </p>
            <p class="text-center">

            <small style=" color:#a3a098">
              <b>Gülşah Alıcı · </b>
              <b style="margin-left: 6p"> {{ timeConvert(post.created_at) }} </b>
            </small>
            </p>
            
            <span v-if="isLoggedIn && router.currentRoute.value.fullPath == '/admin'" style="float:right">
              <el-link :underline="false" type="success" :href="'/admin/posts/' + post.slug" style="margin-right: 3px">
                <el-button type="primary" :icon="Edit" circle />
              </el-link>
              <el-button type="danger" :icon="Delete" circle @click="dialogVisible = true, selectedPost = post"/>
            </span>
          </el-col>

          <el-col :xs="24" :sm="24" :md="10" :lg="8" :xl="12" style="margin-top: 10px; padding: 20px;" class="text-center">
            <el-image :src="post.image" lazy />
          </el-col>
        </el-row>

        <el-row style="padding: 14px" align="middle" justify="center" v-else>
          <el-col>
            <p class="text-center">
              <b>{{ post.title }}</b>
            </p>
            <div class="bottom">
              {{ post.summary }} ..
            </div>
            <p class="text-center">
              <el-link :underline="false" type="primary" :href="'/blog/posts/' + post.slug">      
                Read more
              </el-link>
            </p>
            <p class="text-center">

            <small style=" color:#a3a098">
              <b>Gülşah Alıcı · </b>
              <b style="margin-left: 6p"> {{ timeConvert(post.created_at) }} </b>
            </small>
            </p>
            
            <span v-if="isLoggedIn && router.currentRoute.value.fullPath == '/admin'" style="float:right">
              <el-link :underline="false" type="success" :href="'/admin/posts/' + post.slug" style="margin-right: 3px">
                <el-button type="primary" :icon="Edit" circle />
              </el-link>
              <el-button type="danger" :icon="Delete" circle @click="dialogVisible = true, selectedPost = post"/>
            </span>
          </el-col>
        </el-row>
      </el-card>
    </el-col>
    <el-dialog
      v-model="dialogVisible"
      title="Remove Post"
      width="30%"
    >
      <span>Are you sure want to delete this <b>{{ selectedPost.slug }}</b>?</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="removePost()" >Confirm</el-button
          >
        </span>
      </template>
    </el-dialog>
  </el-row>

  <el-row justify="center" v-loading="loading" v-if="posts.length < postsCount">
    <el-button type="text" @click="fetchMore()">Show more</el-button>
  </el-row>
  
</template>

<script>
import { fetchPosts, deletePost, fetchPostsCount } from '@/firebase'
import { onBeforeMount, ref } from 'vue'
import firebase from 'firebase/app'
import { useRouter } from 'vue-router'
import { Edit, Delete } from '@element-plus/icons'
import { Notebook, MilkTea } from '@element-plus/icons'

export default {
  props: {
    admin: Object
  },
  components: { Notebook, MilkTea },
  setup() {
    const posts = ref([])
    const postsCount = ref(0)
    const loading = ref(true)
    
    fetchPosts().then((data) => {
        posts.value = data
        loading.value = false
    })

    fetchPostsCount().then((data) => {
        postsCount.value = data
        loading.value = false
    })
    
    
    const isLoggedIn = ref(false)
    const router = useRouter()
    const dialogVisible = ref(false)
    const selectedPost = ref(null)

    const removePost = () => {
      deletePost(selectedPost.value.slug)
      dialogVisible.value = false
    }

    const fetchMore = () => {
      fetchPosts().then((data) =>
      {
          posts.value = data
          loading.value = false
      })
    }

    const timeConvert = (timestamp) => {
      var d = new Date(timestamp.seconds * 1000)
      var curr_date = d.getDate()
      var curr_month = d.getMonth() + 1
      var curr_year = d.getFullYear()

      return (curr_date.toString().length == 1 ? '0' : '') + curr_date + "/" + 
      (curr_month.toString().length == 1 ? '0' : '') + curr_month + "/" + curr_year
    }

    onBeforeMount(() => {
        
        firebase.auth().onAuthStateChanged((user) => {
        if(user) {
            isLoggedIn.value = true
        }
        else {
            isLoggedIn.value = false
        }
      }) 
    })

    return {
      posts,
      timeConvert,
      isLoggedIn,
      router,
      Edit,
      Delete,
      dialogVisible,
      removePost,
      selectedPost,
      loading,
      fetchMore,
      postsCount
    }
  }

}
</script>

<style scoped>
.img-border {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 1px;
}
img {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  max-width: 250px;
  width: auto;
}
</style>