<template>
  <el-row justify="center">
    <el-col :xs="16" :sm="13" :md="11" :lg="9" :xl="9">
      <h3 class="text-center">Login</h3>
      
      <el-form :model="form" label-width="73px" class="text-center">
        <el-form-item
          prop="email"
          label="Email"
          :rules="[
            {
              required: true,
              message: 'Please input email address',
              trigger: 'blur',
            },
            {
              type: 'email',
              message: 'Please input correct email address',
              trigger: ['blur'],
            },
          ]"
        >
          <el-input v-model="form.email" />
        </el-form-item>

        <el-form-item 
          label="Password" 
          prop="password"
          :rules="[
            {
              required: true,
              message: 'Please input password',
              trigger: 'blur',
            },
          ]"
        >
          <el-input v-model="form.password" type="password" autocomplete="off" />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit">Login</el-button>
        </el-form-item>

      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import { reactive} from 'vue'
//import { createUser } from '@/firebase'
import { loginUser } from '@/firebase'
import { ElMessage } from 'element-plus'
import { useRouter } from 'vue-router'

export default {
  setup() {
    
    const router = useRouter()

    const form = reactive({
      email: '',
      password: ''
    })

    const onSubmit = async () => {
      /*
      Create user
      await createUser(form)
       
      form.email = ''
      form.password = ''
      */
     await loginUser(form)
      .then(data => {
        ElMessage({
          message: 'Welcome ' + data.user.email.split('@')[0] + ' !',
          type: 'success',
        })
        
        setTimeout(() => router.push({path: '/admin' }), 2000)
      })
      .catch(err => {
        ElMessage({
          message: err.message,
          type: 'error',
        })
      })
    }

    return {
      form,
      onSubmit
    }
  }
}
</script>
