<template>
  <div v-loading="loading">
    <el-row>
      <el-breadcrumb :separator-icon="ArrowRight">
        <el-breadcrumb-item :to="{ path: '/admin' }">Admin</el-breadcrumb-item>
        <el-breadcrumb-item>Posts</el-breadcrumb-item>
        <el-breadcrumb-item v-if="edit == true">{{post.slug}}</el-breadcrumb-item>
        <el-breadcrumb-item v-else>New</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>

    <el-row>
      <el-col class="text-center">
        <h3 >ADMIN PANEL</h3>
      </el-col>
    </el-row>
        
    <el-row justify="center">
      <el-col :xs="24" :sm="24" :md="20" :lg="16" :xl="19">
        <div class="grid-content bg-purple">
          <h3 class="text-center" v-if="edit == true">Edit Post</h3>
          <h3 class="text-center" v-else>New Post</h3>

          <el-form :model="form" label-width="80px">
            <el-form-item label="Title">
              <el-input v-model="form.title" />
            </el-form-item>

            <el-form-item label="Slug">
              <el-input v-model="form.slug" />
            </el-form-item>
            
            <el-form-item label="Summary">
              <el-input v-model="form.summary" :autosize="{ minRows: 2, maxRows: 4 }" type="textarea" placeholder="Please input" />
            </el-form-item>

            <el-form-item label="Created at">
              <el-date-picker v-model="form.created_at" type="date" placeholder="Pick a date" style="width: 100%" />
            </el-form-item>

            <el-form-item label="Image">
              <img v-if="form.image && changed == false" class="el-upload-list__item-thumbnail" :src="form.image" style="width: 150px"/>
              <el-upload action="#" list-type="picture-card" :auto-upload="false" @change="uploadImg">
                <template #default>
                  <el-icon><Plus /></el-icon>
                </template>
                <template #file="{ file }">
                  <div>
                    <img class="el-upload-list__item-thumbnail" :src="file.url" alt="" />
                    <span class="el-upload-list__item-actions">
                      <span
                        class="el-upload-list__item-preview"
                        @click="handlePictureCardPreview(file)"
                      >
                        <el-icon><zoom-in /></el-icon>
                      </span>
                      <span
                        v-if="!disabled"
                        class="el-upload-list__item-delete"
                        @click="handleDownload(file)"
                      >
                        <el-icon><Download /></el-icon>
                      </span>
                      <span
                        v-if="!disabled"
                        class="el-upload-list__item-delete"
                        @click="handleRemove(file)"
                      >
                        <el-icon><Delete /></el-icon>
                      </span>
                    </span>
                  </div>
                </template>
              </el-upload>
            </el-form-item>

            <el-form-item label="Content" v-if="form.content">
              <div>
                <QuillEditor v-model:content="form.content" theme="snow" contentType="html" toolbar="full" ref="quillEditor" style="min-height: 200px"/>
              </div>
            </el-form-item>

            <el-form-item label="Author">
              <el-input v-model="form.author" />
            </el-form-item>
            
            <el-form-item>
              <el-button type="primary" @click="onSubmit">Create Post</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { onBeforeMount, reactive, ref } from 'vue'
import { useRouter } from 'vue-router'
import firebase from 'firebase/app'
import 'firebase/firestore'
import 'firebase/auth'
import { ElMessage } from 'element-plus'
import { Plus, ZoomIn, Download, Delete } from '@element-plus/icons-vue'
import { createPost, updatePost, fetchPosts, fetchPost } from '@/firebase'
import { ArrowRight } from '@element-plus/icons'

export default {
  components: { Plus, ZoomIn, Download, Delete },
  setup() {

    const loading = ref(true)
    const router = useRouter()
    const posts = fetchPosts()
    const edit = ref(false)
    const post = ref(null)
    const form = reactive({
      title: '',
      slug: '',
      summary: '',
      image: '',
      created_at: '',
      content: '',
      author: 'Gülşah Alıcı'
    })
    
    const changed = ref(false)

    const onSubmit = async () => {
      if(edit.value == true) {
        await updatePost(post.value.slug, form)
          .then(() => {
            ElMessage({
              message: 'Post updated!',
              type: 'success',
            })
          })
          .catch(err => {
            ElMessage({
              message: err.message,
              type: 'error',
            })
          })
      }
      else {
        await createPost(form)
          .then(() => {
            ElMessage({
              message: 'Post saved!',
              type: 'success',
            })
          })
          .catch(err => {
            ElMessage({
              message: err.message,
              type: 'error',
            })
          })
      }

      router.push({path: '/admin/posts/' + form.slug})

      setTimeout(() => loadPost(), 1)
    }

    const logout = () => {
      firebase.auth().signOut()
        .then(() => {
          ElMessage({
            message: 'Logged out!',
            type: 'success',
          })
          router.push({path: '/admin/login' })
        })
        .catch(err => {
          ElMessage({
            message: err.message,
            type: 'error',
          })
        })
    }

    const handleRemove = (file) => {
      console.log(file)
    }

    const uploadImg = (event) => {
      changed.value = true
      let reader = new FileReader();
      let file = event.raw;
      reader.readAsDataURL(file);
      reader.onload = function(){
        form.image = reader.result
      }
    }
    const loadPost = () => {
      const slug = router.currentRoute.value.params.slug

      fetchPost(slug).then((data) =>
      {
        if(data) {
          post.value = data
          edit.value = true
          form.title = data.title
          form.slug = data.slug
          form.summary = data.summary
          form.image = data.image
          form.created_at = new Date(data.created_at.seconds * 1000) 
          form.content = data.content
          form.author = data.author
          loading.value = false
        }
        else {
          form.content = ' '
          loading.value = false
        }
      })
      .catch(() => {
        loading.value = false
      })
    }

    onBeforeMount(() => {
      loadPost()
    })

    return {
      logout,
      onSubmit,
      form,
      handleRemove,
      posts,
      uploadImg,
      ArrowRight,
      changed,
      edit,
      post,
      loading
    }
  }
}
</script>