<template>
  <el-container>
    <el-header class="text-center" style="height: auto; padding: 0px">
     <el-row align="middle">
      <el-col :xs="24" :sm="24" :md="4" :lg="4" :xl="4">
        <el-link :underline="false" class="mr-3" href="https://mail.google.com/mail/u/0/#inbox" target="_blank">
          <i class="fa fa-envelope-square font-size-30"></i>
        </el-link>
        <el-link :underline="false" class="mr-3" href="https://github.com/gulsahalici" target="_blank">
          <i class="fa fa-github-square font-size-30"></i>
        </el-link>
        <el-link :underline="false" href="https://www.linkedin.com/in/g%C3%BCl%C5%9Fah-al%C4%B1c%C4%B1-383780140/" target="_blank">
          <i class="fa fa-linkedin-square font-size-30"></i>
        </el-link>
      </el-col>
      <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
        <h1 class="hand-written font-size75 text-center" style="margin-top: 20px  !important; margin-bottom: 20px !important;">Gülşah Alıcı</h1>
      </el-col>
      <el-col :xs="24" :sm="24" :md="4" :lg="4" :xl="4">
        <el-input size="large" placeholder="Search" style="width: 200px;" :suffix-icon="Search"></el-input>
      </el-col>
    </el-row>
    
    <el-menu
      :default-active="changeRoute"
      mode="horizontal"
      background-color="#a66081"
      text-color="#fff"
      active-text-color="#ffd04b"
      style="margin-top: 5px"
      :key="route"
    >
      <el-link :underline="false" class="mr-5" href="/">      
        <el-menu-item index="Home">Home</el-menu-item>
      </el-link>
      <el-link :underline="false" class="mr-5" href="/blog">      
        <el-menu-item index="Blog">Blog</el-menu-item>
      </el-link>
      <el-link :underline="false" class="mr-5" href="/about-me">      
        <el-menu-item index="About">About me</el-menu-item>
      </el-link>
      
      <el-sub-menu index="Admin" v-if="isLoggedIn">
        <template #title>Gülşah Alıcı</template>
        <el-link :underline="false" class="mr-5" href="/admin">      
          <el-menu-item index="Admin-1">Admin Page</el-menu-item>
        </el-link>
        
        <el-menu-item index="Admin-2">
          <el-button type="text" @click="logout">
            <el-menu-item index="2-1"  style="width: 200px">Logout</el-menu-item>
          </el-button>
        </el-menu-item>
      </el-sub-menu>
    </el-menu>
    </el-header>
    <el-main class="main" style="height: auto">
      <slot/>
    </el-main>
    <el-footer class="text-center" style="background-color: #a66081;height: auto; padding: 5px">
      <small style="color: white"><b>©2022 Gülşah Alıcı</b></small>
    </el-footer>
  </el-container>
</template>

<script>
import { onBeforeMount, ref, computed } from 'vue'
import firebase from 'firebase/app'
import 'firebase/firestore'
import 'firebase/auth'
import { Search } from '@element-plus/icons'
import { ElMessage } from 'element-plus'
import { useRouter, useRoute } from 'vue-router'

export default {
  name: 'App',
  setup() {
    const isLoggedIn = ref(false)

    const router = useRouter()
    const route = useRoute()
    const activeTab = ref('Admin')
    
    const logout = () => {
      firebase.auth().signOut()
        .then(() => {
          ElMessage({
            message: 'Logged out!',
            type: 'success',
          })
          router.push({path: '/admin/login' })
        })
        .catch(err => {
          ElMessage({
            message: err.message,
            type: 'error',
          })
        })
    }

    onBeforeMount(() => {  
      firebase.auth().onAuthStateChanged((user) => {
        if(user) {
          isLoggedIn.value = true
        }
        else {
          isLoggedIn.value = false
        }
      })
      console.log(route)
      activeTab.value = route.name

      if(route.name == 'Admin' || route.name == 'Admin-Post-Create') {
        console.log("eyy")
        activeTab.value = route.name + '-1'
      }
    })
    
    const changeRoute = computed(() => {
      if(route.name == 'Admin' || route.name == 'Admin-Post-Create') {
        return 'Admin-1'
      }
      else if(route.name == 'About') {
        return 'About'
      }

      return 'Blog'
    })

    return {
      isLoggedIn,
      Search,
      logout,
      activeTab,
      changeRoute
    }
  }
}
</script>

<style scoped>
.mr-3 {
  margin-right: 3px;
}
.colorWhite {
  color: white;
}
