<template>
  <div v-loading="loading">
    <el-row>
      <el-breadcrumb :separator-icon="ArrowRight">
        <el-breadcrumb-item :to="{ path: '/blog' }">Blog</el-breadcrumb-item>
        <el-breadcrumb-item>Posts</el-breadcrumb-item>
        <el-breadcrumb-item v-if="post">{{ post.title }}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row justify="center">
      <el-col :span="18" v-if="post" class="html-img">
        <h2 class="text-center"> {{post.title}} </h2>

        <el-row justify="center">
          <el-col :span="1">
            <el-avatar :size="45" src="https://empty" style="float:right">
              <img src="../../../public/pp-img2.jpg"/>
            </el-avatar>
          </el-col>
        </el-row>
        <el-row justify="center" class="text-center">
          <el-col :span="4">
             <b style="color:#a3a098"><small>Gülşah Alıcı · {{ timeConvert(post.created_at) }}</small></b> 
          </el-col>
        </el-row>

        <div v-html="post.content"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { useRouter } from 'vue-router'
import { fetchPost } from '@/firebase'
import 'firebase/firestore'
import 'firebase/auth'
import { ref } from 'vue'
import { ArrowRight } from '@element-plus/icons'

export default {
  setup() {
    const router = useRouter()

    const slug = router.currentRoute.value.params.slug

    var post = ref(null)
    const loading = ref(true)

    fetchPost(slug).then((data) =>
    {
        console.log("hey",data)
        post.value = data
        loading.value = false
    })
    

    const timeConvert = (timestamp) => {
      var d = new Date(timestamp.seconds * 1000)
      var curr_date = d.getDate()
      var curr_month = d.getMonth() + 1
      var curr_year = d.getFullYear()

      return (curr_date.toString().length == 1 ? '0' : '') + curr_date + "/" + 
      (curr_month.toString().length == 1 ? '0' : '') + curr_month + "/" + curr_year
    }

    return {
      post,
      timeConvert,
      loading,
      ArrowRight
    }
  }
}
</script>

